
const useAddTimer = (cb, time = 1000) => {
  let timer = null

  onBeforeMount(() => {
    timer = setInterval(cb, time)
  })

  onBeforeUnmount(() => {
    clearInterval(timer)
    timer = null
  })
}

const zeroPadding = (value) => {
  return value.toString().length == 1 ? `0${value}` : value
}
const useCountdown = (time) => {
  const countdown = reactive({
    day: '',
    hours: '',
    minutes: '',
    seconds: '',
    exceed: false,
  })

  const getCountdown = () => {
    const now = dayjs()
    const t = isFunc(time) ? time() : time
    const diffTime = dayjs.duration(t - now)
    const day = diffTime.days() // 天
    const hours = diffTime.hours() // 小时
    const minutes = diffTime.minutes() // 分钟
    const seconds = diffTime.seconds() // 秒
    return { day: Math.max(day, 0), hours: Math.max(hours, 0), minutes: Math.max(minutes, 0), seconds: Math.max(seconds, 0), exceed: t - now < 0 }
  }

  const refreshCountdown = () => {
    const { day, hours, minutes, seconds, exceed } = getCountdown()

    countdown.day = zeroPadding(isNaN(day) ? '00' : day)
    countdown.hours = zeroPadding(isNaN(hours) ? '00' : hours)
    countdown.minutes = zeroPadding(isNaN(minutes) ? '00' : minutes)
    countdown.seconds = zeroPadding(isNaN(seconds) ? '00' : seconds)
    countdown.exceed = exceed
  }

  useAddTimer(refreshCountdown)

  return { countdown }
}

export { useCountdown, useAddTimer }
